.tabs__list {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 0;
  padding: 0;
  overflow-x: auto;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch;

  list-style: none;

  -ms-overflow-style: none;
  scrollbar-width: none;
}

.tabs__item {
  display: block;
  flex-shrink: 0;
  padding: 20px 23px 11px;

  border-bottom: 2px solid $color-gray-transparent-20;
  cursor: pointer;

  @include vp-1199 {
    padding: 20px 27px 11px;
  }
  @include vp-767 {
    padding: 20px 22px 11px;
  }

  &--active {
    cursor: auto;
  }
}

.tabs__item:first-child {
  padding-left: 0;
}

.tabs__item:last-child {
  padding-right: 0;
}

.tabs__item a {
  font-size: 20px;
  line-height: 30px;
  color: $color-sapphire-transparent-50;

  user-select: none;

  @include vp-1199 {
    font-size: 18px;
    line-height: 27px;
  }
  @include vp-767 {
    font-size: 16px;
    line-height: 24px;
  }
}

.tabs__item:hover a,
.tabs__link:focus a,
.tabs__link:active a {
  opacity: 0.8;
}

.tabs__item--active a {
  padding-bottom: 14px;

  color: $color-sapphire;

  border-bottom: 2px solid $color-crimson;
  cursor: auto;
}

.tabs__item--active:hover a,
.tabs__link--active:focus a,
.tabs__link--active:active a {
  opacity: 1;
}
